import React, {Component} from 'react';
import {apiBaseUrl} from "../config";
import * as axios from "axios";
import {Redirect} from "react-router-dom";

export default class RegisterUser extends Component {
    constructor(props) {
        super(props);

        this.state = {
            isComplete: false,
            firstName: "",
            lastName: "",
            email: "",
            phoneNumber: "",
            password: "",
            confirmPassword: "",
            isFirstNameValid: true,
            isLastNameValid: true,
            isEmailValid: true,
            isPhoneNumberValid: true,
            isPasswordValid: true,
            isConfirmPasswordValid: true,
            isEmailAlreadyUsed: false,
            errors: {
                firstName: '',
                lastName: '',
                email: '',
                phoneNumber: '',
                password: '',
                confirmPassword: ''
            }
        };
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    async componentDidMount() {
        this.updateFormUserData();
    }

    async componentWillReceiveProps() {
        this.updateFormUserData();
    }

    updateFormUserData() {
        this.setState({
            firstName: (this.props.authUser) ? this.props.authUser.first_name : '',
            lastName: (this.props.authUser) ? this.props.authUser.last_name : '',
            email: (this.props.authUser) ? this.props.authUser.email : '',
            phoneNumber: (this.props.authUser) ? this.props.authUser.phone_number : '',
        })
    }

    updateAuthUser(response) {
        this.props.onChangeUser(response.data.user);
    }

    validateForm() {
        this.setState({
            isFirstNameValid: (this.validateFirstName(this.state.firstName)) ? true : false,
            isLastNameValid: (this.validateLastName(this.state.lastName)) ? true : false,
            isEmailValid: (this.validateEmail(this.state.email)) ? true : false,
            isPhoneNumberValid: (this.validatePhoneNumber(this.state.phoneNumber)) ? true : false,
            isPasswordValid: (this.validatePassword(this.state.password)) ? true : false,
            isConfirmPasswordValid: (this.validateConfirmPassword(this.state.password, this.state.confirmPassword)) ? true : false
        });

        return this.state.isFirstNameValid &&
            this.state.isLastNameValid &&
            this.state.isEmailValid &&
            this.state.isPhoneNumberValid &&
            this.state.isPasswordValid &&
            this.state.isConfirmPasswordValid
    }

    validateFirstName(firstName) {
        if (!firstName) {
            this.setState(prevState => ({
                errors: {
                    ...prevState.errors,
                    firstName: 'First Name is required'
                }
            }));
            return false;
        }

        this.setState(prevState => ({
            errors: {
                ...prevState.errors,
                firstName: ''
            }
        }));

        return true;
    }

    validateLastName(lastName) {
        if (!lastName) {
            this.setState(prevState => ({
                errors: {
                    ...prevState.errors,
                    lastName: 'Last Name is required'
                }
            }));
            return false;
        }

        this.setState(prevState => ({
            errors: {
                ...prevState.errors,
                lastName: ''
            }
        }));

        return true;
    }

    validateEmail(email) {
        if (this.props.authUser) {
            return true;
        }
        const emailRegex = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

        if (!emailRegex.test(email)) {
            this.setState(prevState => ({
                errors: {
                    ...prevState.errors,
                    email: 'Invalid email'
                }
            }));
            return false;
        }

        this.checkIfUserExists(email);

        this.setState(prevState => ({
            errors: {
                ...prevState.errors,
                email: ''
            }
        }));

        return true;
    }

    validatePhoneNumber(phoneNumber) {
        const phoneNumberRegex = /^\(?(\d{2})\)?[- ]?(\d{4})[- ]?(\d{4})$/;

        if (!phoneNumberRegex.test(phoneNumber)) {
            this.setState(prevState => ({
                errors: {
                    ...prevState.errors,
                    phoneNumber: 'Invalid phone number'
                }
            }));
            return false;
        }

        this.setState(prevState => ({
            errors: {
                ...prevState.errors,
                phoneNumber: ''
            }
        }));

        return true;
    }

    validatePassword(password) {
        if (!password && this.props.authUser) {
            return true;
        }
        const passwordRegex = /^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{8,20}$/;

        if (!passwordRegex.test(password)) {
            this.setState(prevState => ({
                errors: {
                    ...prevState.errors,
                    password: 'Invalid password'
                }
            }));
            return false;
        }

        this.setState(prevState => ({
            errors: {
                ...prevState.errors,
                password: ''
            }
        }));

        return true;
    }

    validateConfirmPassword(password, confirmPassword) {
        if (password !== confirmPassword) {
            this.setState(prevState => ({
                errors: {
                    ...prevState.errors,
                    confirmPassword: 'Confirm password is not matching'
                }
            }));
            return false;
        }

        this.setState(prevState => ({
            errors: {
                ...prevState.errors,
                confirmPassword: ''
            }
        }));

        return true;
    }

    checkIfUserExists(email) {
        var self = this;
        axios({
            method: 'POST',
            url: apiBaseUrl + '/user/check-if-exist',
            data: {
                "email": email
            },
            crossDomain: true,
            headers: {
                'Content-Type': 'application/json',
            },
        })
            .then(function (response) {
                self.setState({
                    isEmailAlreadyUsed: response.data.user
                })
            })
            .catch(function (error) {
                console.log(error);
            });


    }
    catch(e) {
        alert(e);
    }

handleSubmit = async event => {
    event.preventDefault();
    if (! this.validateForm()) {
        return;
    }

    try {
        var self = this;
        let method = (this.props.authUser)? ((this.props.authUser.id) ? 'PUT' : 'POST') : 'POST';
        let url = (this.props.authUser)? ((this.props.authUser.id)? '/user/' + this.props.authUser.id : '/user/register' ) : '/user/register';
        let data;
        if ((this.props.authUser)? ((this.props.authUser.id) ? true : false) : false) {
            data = {
                "first_name": this.state.firstName,
                "last_name": this.state.lastName,
                "phone_number": this.state.phoneNumber,
                "password": this.state.password
            }
        } else {
            data = {
                "first_name": this.state.firstName,
                "last_name": this.state.lastName,
                "phone_number": this.state.phoneNumber,
                "email": this.state.email,
                "password": this.state.password
            }
        }
        axios({
            method: method,
            url: apiBaseUrl + url,
            data: data,
            crossDomain: true,
            headers: {
                'Content-Type': 'application/json',
            },
        })
            .then(function (response) {
                self.updateAuthUser(response);
                self.setState({
                    isComplete: true
                })
            })
            .catch(function (error) {
                console.log(error);
            });

    } catch (e) {
        alert(e);
    }

};

handleChange = event => {
    this.setState({
        [event.target.id]: event.target.value
    });
};

render()
{
    const next = (this.props.links) ? this.props.links.next : null;
    const businessCategory = this.props.businessCategory;
    return (
        businessCategory ?
        <div>
            {this.state.isComplete &&
            <Redirect to={next}/>
            }
            <form className="apps-form" onSubmit={this.handleSubmit}>
                <div className="form-group">
                    <label>First Name<span>*</span></label>
                    <input type="text" className={`${(!this.state.isFirstNameValid) ? 'form-error' : ''} form-control`}
                           id="firstName" placeholder="eg. John"
                           autoFocus
                           value={(this.state.firstName)}
                           onChange={this.handleChange}
                    />
                    <small className="form-error-message">{this.state.errors.firstName}</small>
                </div>
                <div className="form-group">
                    <label>Last Name<span>*</span></label>
                    <input type="text" className={`${(!this.state.isLastNameValid) ? 'form-error' : ''} form-control`}
                           id="lastName" placeholder="eg. Smith"
                           value={this.state.lastName}
                           onChange={this.handleChange}
                    />
                    <small className="form-error-message">{this.state.errors.lastName}</small>
                </div>
                <div className="form-group">
                    <label>Your email address <span>*</span></label>
                    <input type="email" className={`${(! (this.state.isEmailValid && !this.state.isEmailAlreadyUsed)) ? 'form-error' : ''} form-control`}
                           id="email" placeholder="eg. john.smith@gmail.com"
                           value={this.state.email}
                           onChange={this.handleChange}
                    />
                    <small className="form-error-message">{this.state.errors.email}</small>
                    <small className="form-error-message">{this.state.isEmailAlreadyUsed && 'Email already registered'}</small>
                </div>
                <div className="form-group">
                    <label>Your phone number <span>*</span></label>
                    <input type="tel" className={`${(!this.state.isPhoneNumberValid) ? 'form-error' : ''} form-control`}
                           id="phoneNumber" placeholder="eg. (02) 9855 0000"
                           value={this.state.phoneNumber}
                           onChange={this.handleChange}
                    />
                    <small className="form-error-message">{this.state.errors.phoneNumber}</small>
                </div>
                <div className="form-group">
                    <label>Create a password <span>*</span></label>
                    <input type="password"
                           className={`${(!this.state.isPasswordValid) ? 'form-error' : ''} form-control`} id="password"
                           placeholder="xxxxxx"
                           value={this.state.password}
                           onChange={this.handleChange}
                    />
                    <small className="form-error-message">{this.state.errors.password}</small>
                </div>
                <small className="note">Must include lower, upper, number and symbol min 8 characters up to 20.
                </small>
                <div className="form-group">
                    <label>Confirm password <span>*</span></label>
                    <input type="password"
                           className={`${(!this.state.isConfirmPasswordValid) ? 'form-error' : ''} form-control`}
                           id="confirmPassword" placeholder="xxxxxx"
                           value={this.state.confirmPassword}
                           onChange={this.handleChange}
                    />
                    <small className="form-error-message">{this.state.errors.confirmPassword}</small>
                </div>

                <div className="btn-wrap">
                    {next && <button type="submit" className="btn btn-default btn-md">Continue</button>}
                </div>
            </form>
        </div>
            :
            <Redirect to='/levels/1/sections/2/business-options/2'/>
    )
}
}


