import React, {Component} from 'react';
import {getBusinessCategories} from "../libs/awsLib";
import {Link} from "react-router-dom";
import * as cookie from "react-dom";

export default class BusinessCategories extends Component {
    constructor(props) {
        super(props);

        this.state = {
            selectedBusinessCategory: null,
            businessCategories: null,
            active: null,
            showSellGoodsOption: false,
        };
    }

    async componentDidMount(){
        this.getBusinessCategories();
    }

    async getBusinessCategories() {
        try {
            let businessCategories;
            if (businessCategories = await getBusinessCategories()) {
                this.setState({ businessCategories: businessCategories });
            }
        }
        catch (e) {
            alert(e);
        }
    }

    handleSelect(e, id) {
        e.preventDefault();

        this.props.onChangeBusinessCategory(id);

        this.setState({
            showSellGoodsOption: (id != 4) ? true: false,
            active: id
        });
    }

    handleSelectSellGoods(e, sellGoods) {
        e.preventDefault();

        this.props.onChangeSellGoods(sellGoods);
    }

    handleToolTip(e, tooltip) {
        e.preventDefault();
        this.props.onChangeToolTip(tooltip);
    }

    render() {
        var style = {
            maxWidth: 150 + 'px'
        };
        const next = (this.props.links && this.props.selectedBusinessCategory !== null) ? this.props.links.next : null;
        const businessCategories = (this.state.businessCategories) && this.state.businessCategories.data.map((item, index) => {
            const active = (this.state.active == item.id) || this.props.businessCategory == item.id ? 'active' : '';
            return (
                <li key={item.id} style={ style } className={ active }>
                    <div onClick={(e) => this.handleSelect(e, item.id)}>
                        <a className="white-icon" href="#">
                            <img src={ item.white_icon } alt="" />
                        </a>
                        <a className="red-icon" href="#">
                            <img src={ item.red_icon } alt="" />
                        </a>
                        <span> <a href="#">{ item.name }</a></span>
                    </div>
                    <a className="apps-question" href="#" onClick={(e) => this.handleToolTip(e, item.tooltip)}><i className="fa fa-lightbulb-o" aria-hidden="true"></i>
                    </a>
                </li>
            )
        });

        const sellGoodsCategory = (this.state.businessCategories) && this.state.businessCategories.data.map((item, index) => {
            const active = (this.state.active == item.id) ? 'active' : '';
            return (
                (this.props.businessCategory === item.id) &&
                <li key={item.id} style={ style } className={ active }>
                    <a className="white-icon" href="#">
                        <img src={ item.white_icon } alt="" />
                    </a>
                    <a className="red-icon" href="#">
                        <img src={ item.red_icon } alt="" />
                    </a>
                    <span> <a href="#">{ item.name }</a></span>
                    <a className="apps-question" href="#"><i className="fa fa-lightbulb-o" aria-hidden="true"></i>
                    </a>
                </li>
            )
        });

        const sellGoodsQuestion = (
            <div>
                <ul className="apps-icons clearfix">
                    { sellGoodsCategory}
                </ul>
                <h3>Will you be selling goods online?</h3>
                <div className="selling-goods-btns">
                    <a href="" className={`btn btn-default btn-sm ${this.props.sellGoods ? 'active' : ''}`} onClick={(e) => this.handleSelectSellGoods(e, true)}>Yes</a>
                    <a href="" className={`btn btn-default btn-sm ${this.props.sellGoods ? '' : 'active'}`} onClick={(e) => this.handleSelectSellGoods(e, false)}>No</a>
                </div>
            </div>
        );

        return (
            <div>
                <ul className="apps-icons clearfix apps-h-effect">
                    { (this.state.showSellGoodsOption) ? sellGoodsQuestion : businessCategories }
                </ul>
                <div className="btn-wrap">
                    {next && <Link to={next} className="btn btn-default btn-md">Continue</Link>}
                </div>
            </div>

        )
    }
}


