import React, {Component} from 'react';
import GettingStartedHome from "../elements/GettingStartedHome";
import BusinessCategories from "../elements/BusinessCategories";
import RegisterUser from "../elements/RegisterUser";
import CreateBusiness from "../elements/CreateBusiness";
import RegisterBusiness from "../elements/RegisterBusiness";
import LevelOneComplete from "../elements/LevelOneComplete";

export default class BusinessOption extends Component {
    render() {
        const current = this.props.currentBusinessOption;
        const content = (current.data) ? current.data.content : '';
        const dynamicElement = (current.data) ? current.data.element : '';
        return (
            <div>
                <div>{content}</div>
                <div>
                    {(dynamicElement == 'GettingStartedHome') ? <GettingStartedHome
                        currentBusinessOption={current}
                        authUser={this.props.authUser}
                        business={this.props.business}
                        businessCategory={this.props.businessCategory}
                        sellGoods={this.props.sellGoods}
                        onChangeUser={ (authUser) => this.props.onChangeUser(authUser) }
                        onChangeBusiness={ (business) => this.props.onChangeBusiness(business) }
                        onChangeBusinessCategory={ (businessCategory) => this.props.onChangeBusinessCategory(businessCategory) }
                        onChangeSellGoods={ (sellGoods) => this.props.onChangeSellGoods(sellGoods) }
                        onChangeToolTip={ (tooltip) => this.props.onChangeToolTip(tooltip)}
                        links={ current.links }/> : '' }
                    {(dynamicElement == 'BusinessCategories') ? <BusinessCategories
                        currentBusinessOption={current}
                        authUser={this.props.authUser}
                        business={this.props.business}
                        businessCategory={this.props.businessCategory}
                        sellGoods={this.props.sellGoods}
                        onChangeUser={ (authUser) => this.props.onChangeUser(authUser) }
                        onChangeBusiness={ (business) => this.props.onChangeBusiness(business) }
                        onChangeBusinessCategory={ (businessCategory) => this.props.onChangeBusinessCategory(businessCategory) }
                        onChangeSellGoods={ (sellGoods) => this.props.onChangeSellGoods(sellGoods) }
                        onChangeToolTip={ (tooltip) => this.props.onChangeToolTip(tooltip)}
                        links={ current.links } /> : '' }
                    {(dynamicElement == 'RegisterUser') ? <RegisterUser
                        currentBusinessOption={current}
                        authUser={this.props.authUser}
                        business={this.props.business}
                        businessCategory={this.props.businessCategory}
                        sellGoods={this.props.sellGoods}
                        onChangeUser={ (authUser) => this.props.onChangeUser(authUser) }
                        onChangeBusiness={ (business) => this.props.onChangeBusiness(business) }
                        onChangeBusinessCategory={ (businessCategory) => this.props.onChangeBusinessCategory(businessCategory) }
                        onChangeSellGoods={ (sellGoods) => this.props.onChangeSellGoods(sellGoods) }
                        onChangeToolTip={ (tooltip) => this.props.onChangeToolTip(tooltip)}
                        links={ current.links }/> : '' }
                    {(dynamicElement == 'CreateBusiness') ? <CreateBusiness
                        currentBusinessOption={current}
                        authUser={this.props.authUser}
                        business={this.props.business}
                        businessCategory={this.props.businessCategory}
                        sellGoods={this.props.sellGoods}
                        onChangeUser={ (authUser) => this.props.onChangeUser(authUser) }
                        onChangeBusiness={ (business) => this.props.onChangeBusiness(business) }
                        onChangeBusinessCategory={ (businessCategory) => this.props.onChangeBusinessCategory(businessCategory) }
                        onChangeSellGoods={ (sellGoods) => this.props.onChangeSellGoods(sellGoods) }
                        onChangeToolTip={ (tooltip) => this.props.onChangeToolTip(tooltip)}
                        links={ current.links }/> : '' }
                    {(dynamicElement == 'RegisterBusiness') ? <RegisterBusiness
                        currentBusinessOption={current}
                        authUser={this.props.authUser}
                        business={this.props.business}
                        businessCategory={this.props.businessCategory}
                        sellGoods={this.props.sellGoods}
                        onChangeUser={ (authUser) => this.props.onChangeUser(authUser) }
                        onChangeBusiness={ (business) => this.props.onChangeBusiness(business) }
                        onChangeBusinessCategory={ (businessCategory) => this.props.onChangeBusinessCategory(businessCategory) }
                        onChangeSellGoods={ (sellGoods) => this.props.onChangeSellGoods(sellGoods) }
                        onChangeToolTip={ (tooltip) => this.props.onChangeToolTip(tooltip)}
                        links={ current.links }/> : '' }
                    {(dynamicElement == 'LevelOneComplete') ? <LevelOneComplete
                        currentBusinessOption={current}
                        authUser={this.props.authUser}
                        business={this.props.business}
                        businessCategory={this.props.businessCategory}
                        sellGoods={this.props.sellGoods}
                        onChangeUser={ (authUser) => this.props.onChangeUser(authUser) }
                        onChangeBusiness={ (business) => this.props.onChangeBusiness(business) }
                        onChangeBusinessCategory={ (businessCategory) => this.props.onChangeBusinessCategory(businessCategory) }
                        onChangeSellGoods={ (sellGoods) => this.props.onChangeSellGoods(sellGoods) }
                        onChangeToolTip={ (tooltip) => this.props.onChangeToolTip(tooltip)}
                        links={ current.links }/> : '' }
                </div>
            </div>
        )
    }
}


