import React, {Component} from "react";
import {getBusinessOptions} from "../libs/awsLib";

import BusinessOption from "../components/BusinessOption";
import ProgressBar from "../components/ProgressBar";
import ToolTip from "../components/ToolTip";

export default class Page extends Component {
    constructor(props) {
        super(props);

        this.state = {
            completedPercent: 0,
            businessOptions: {},
            tooltip: null
        };
    }

    async componentDidMount(){
        this.getBusinessOptions()
    }

    async componentWillReceiveProps(nextProps){
        if (this.props.location.pathname != nextProps.location.pathname) {
            this.getBusinessOptions(nextProps.location.pathname)
        }
    }

    async getBusinessOptions(link = null) {
        try {
            if (!link) {
                link = this.props.location.pathname;
            }
            let businessOptions;
            if (businessOptions = await getBusinessOptions(link)) {
                this.setState({
                    businessOptions: businessOptions,
                    tooltip: businessOptions.data.tooltip
                });
            }
        }
        catch (e) {
            alert(e);
        }
    }

    handleChangeToolTip(tooltip) {
        this.setState({
            tooltip: tooltip
        });
    }

    render() {
        const data = (this.state.businessOptions.data) ? this.state.businessOptions.data : {};
        const name = data.name;
        return (
            <div>
                <section className="mid-sec bg-red mCustomScrollbar" data-mcs-theme="dark">
                    <div className="content-wrapper step-one">
                        <h5 className="obvious-h5">Getting Started</h5>
                        <ProgressBar />
                        <h1>{ name }</h1>
                        <BusinessOption
                            authUser={this.props.authUser}
                            business={this.props.business}
                            businessCategory={this.props.businessCategory}
                            sellGoods={this.props.sellGoods}
                            currentBusinessOption={ this.state.businessOptions }
                            onChangeUser={ (authUser) => this.props.onChangeUser(authUser) }
                            onChangeBusiness={ (business) => this.props.onChangeBusiness(business) }
                            onChangeBusinessCategory={ (businessCategory) => this.props.onChangeBusinessCategory(businessCategory) }
                            onChangeSellGoods={ (sellGoods) => this.props.onChangeSellGoods(sellGoods) }
                            onChangeToolTip={ (tooltip) => this.handleChangeToolTip(tooltip)}
                        />
                    </div>
                </section>
                <ToolTip tooltip={ this.state.tooltip }/>
            </div>
        );
    }
}



